.reviews{
  padding: 8.3rem 0 12.7rem 0;
  position: relative;
  background: url("../img/promo/promo_pink_swirl.webp")  -17.14% 115.3% / 33.37% no-repeat;
  .container{
    position: relative;
    .reviews__triangle_bg{
      @include bg;
      width: 18rem;
      height: 18rem;
      position: absolute;
      top: -5rem;
      left: 73.1rem;
      z-index: -1;
    }
    .reviews__title{
      text-align: left;
      width: 66rem;
    }
    .reviews__collage{
      position: absolute;
      width: 66.5rem;
      height: 51rem;
      top: 10rem;
      .reviews__collage_img{
        border-radius: 2.1rem;
        border: 0.8rem solid #fff;
        box-shadow: 0 0 1.6rem 0 #C5D8EB;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: absolute;
      }
      .reviews__collage_img1{
        width: 39%;
        height: 29.4%;
        transform: rotate(-15deg);
        background-image: url("../img/reviews/collage/reviews_collage_img1.webp");
        top: 5%;
        left: 2%;
      }
      .reviews__collage_img2{
        width: 36%;
        height: 73.5%;
        transform: rotate(-15deg);
        background-image: url("../img/reviews/collage/reviews_collage_img2.webp");
        top: 20%;
        left: 25%;
      }
      .reviews__collage_img3{
        width: 45.11%;
        height: 39.2%;
        transform: rotate(-15deg);
        background-image: url("../img/reviews/collage/reviews_collage_img3.webp");
        top: 27%;
        left: 50%;
      }
      .reviews__collage_img1,
      .reviews__collage_img2,
      .reviews__collage_img3{
        z-index: 1;
      }
      .reviews__top_ellipse,
      .reviews__bottom_ellipse{
        position: absolute;
        z-index: -1;
      }
      .reviews__top_ellipse{
        @include bg;
        width: 95.2%;
        height: 108.2%;
        top: -4%;
        left: -13%;
      }
      .reviews__bottom_ellipse{
        @include bg;
        width: 82.7%;
        height: 101.37%;
        top: 3%;
        left: 4%;
      }
    }
    .reviews__list_wrapper{
      margin: 8.5rem 0 0 auto;
      width: 46rem;
      .reviews__list{
        .reviews__li{
          list-style: none;
          font-size: 2.4rem;
          font-weight: 500;
          line-height: 265%;
          letter-spacing: -0.06rem;
        }
        .reviews__li::before{
          content: '';
          display: inline-block;
          width: 1.2rem;
          height: 1.2rem;
          border-radius: 0.2rem;
          background: #55D49F;
          margin-right: 2.8rem;
        }
      }
      .reviews__button{
        margin-top: 2.4rem;
        height: 6rem;
        font-size: 2.4rem;
      }
      .reviews__comment{
        margin-top: 2.4rem;
        font-size: 1.6rem;
        font-weight: 300;
        line-height: normal;
        text-align: center;
      }
    }
    .reviews__slider_wrapper{
      margin-top: 9.4rem;
      position: relative;
      .swiper{
        margin-top: 4.4rem;
        width: 102.22rem;
        .reviews__slider{
          width: 102.2rem;
          .reviews__slider_item{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            padding: 0 1rem;
            .reviews__user_photo{
              width: 16.5rem;
              height: 16.5rem;
              border: 0.6rem solid #545F7E;
              border-radius: 100%;
              object-position: center;
              object-fit: cover;
            }
            .reviews__slider_item_text{
              margin-top: 0.4rem;
              width: 30rem;
              .reviews__user_name_wrapper{
                font-size: 2rem;
                font-weight: 700;
                letter-spacing: -0.05rem;
                text-transform: uppercase;
                line-height: normal;
                display: flex;
                flex-direction: column;
                .reviews__user_name{
                  display: block;
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                }
                .reviews__user_nickname{
                  display: block;
                  overflow: hidden;
                  font-weight: 300;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                }
              }
              .reviews__user_review{
                margin-top: 2rem;
                font-size: 1.4rem;
                font-weight: 300;
                line-height: normal;
                letter-spacing: -0.035rem;
              }
            }
          }
        }
      }
      .reviews__prev_btn,
      .reviews__next_btn{
        @include bg;
        width: 2.1rem;
        height: 5.4rem;
        top: 13.35rem;
      }
      .reviews__prev_btn::after,
      .reviews__next_btn::after{
        content: none;
      }
      .reviews__prev_btn{
        left: 0;
      }
      .reviews__next_btn{
        right: 0;
      }
    }
  }
}

@include media('max', 'xl'){
  .reviews{
    background: url("../img/promo/promo_pink_swirl.webp")  -45.14% 113.3% / 39.37% no-repeat;
  }
}

@include media('max', 'l'){
  .reviews{
    background: url("../img/promo/promo_pink_swirl.webp")  -89.14% 119.3% / 51.37% no-repeat;
  }
}

@include media('max', 'm'){
  .reviews{
    background: url("../img/promo/promo_pink_swirl.webp")  -5.1% 103% / 20.3% no-repeat;
    padding: 5.5rem 0 7.5rem 0;
    .container{
      .reviews__triangle_bg{
        top: -2rem;
        left: 79.1rem;
      }
      .reviews__title{
        width: 56rem;
        font-size: 2.4rem;
      }
      .reviews__collage{
        width: 55.3rem;
        height: 42.4rem;
        top: 10.5rem;
        left: -9.4rem;
        .reviews__bottom_ellipse{
          top: 11%;
          left: 6%;
        }
      }
      .reviews__list_wrapper{
        margin: 1.3rem 0 0 auto;
      }
      .reviews__slider_wrapper{
        margin-top: 6.9rem;
        .reviews__slider_title{
          font-size: 2.4rem;
        }
        .swiper{
          margin-top: 4.9rem;
          width: 85rem;
          .reviews__slider{
            .reviews__slider_item{
              .reviews__user_photo{
                width: 12.8rem;
                height: 12.8rem;
              }
              .reviews__slider_item_text{
                width: 26rem;
              }
            }
          }
        }
      }
    }
  }
}

@include media('max', 's') {
  .reviews{
    padding: 4rem 0 6rem 0;
    background: url("../img/reviews/pink_swirl_mobile.webp")  -86.46% 73.25% / 58% no-repeat;
    .container{
      .reviews__triangle_bg{
        display: none;
      }
      .reviews__title{
        text-align: center;
        width: 100%;
      }
      .reviews__collage{
        position: relative;
        width: 49.5rem;
        height: 38rem;
        top: auto;
        left: -5.1rem;
        margin-top: 1.6rem;
        .reviews__top_ellipse{
          width: 129.2%;
          height: 139.2%;
          top: -12%;
          left: -44%;
          transform: rotate(5deg);
        }
        .reviews__bottom_ellipse{
          width: 94.7%;
          height: 115.37%;
          top: 96%;
          left: -6%;
          transform: rotate(6deg);
        }
      }
      .reviews__list_wrapper{
        margin: 0 auto;
        width: 100%;
        .reviews__list{
          .reviews__li{
            font-size: 1.8rem;
            line-height: 232%;
            letter-spacing: -0.045rem;
          }
          .reviews__li::before{
            margin-right: 1.3rem;
          }
        }
        .reviews__button{
          margin-top: 2.3rem;
        }
        .reviews__comment{
          margin-top: 1.7rem;
          font-size: 1.3rem;
        }
      }
      .reviews__slider_wrapper{
        margin-top: 13rem;
        .reviews__slider_title{
          display: flex;
          flex-direction: column;
        }
        .swiper{
          margin-top: 1.3rem;
          width: 100%;
          .reviews__slider{
            .reviews__slider_item{
              display: block;
              padding: 0 3.3rem;
              position: relative;
              .reviews__user_photo{
                width: 11.4rem;
                height: 11.4rem;
                position: absolute;
                top: 0;
                left: 3.3rem;
              }
              .reviews__slider_item_text{
                margin-top: 0;
                width: 100%;
                .reviews__user_name_wrapper{
                  width: 50%;
                  height: 11.4rem;
                  margin: 0 0 0 auto;
                  justify-content: center;
                  font-size: 1.8rem;
                  letter-spacing: -0.045rem;
                }
                .reviews__user_review{
                  margin-top: 1.8rem;
                }
              }
            }
          }
        }
      }
    }
  }
}